import React from "react";
import { graphql, Link } from "gatsby";
import Layout from "../components/layout";

const IndexPage = ({ data }) => {
  const posts = data.allMarkdownRemark.edges;

  return (
    <Layout>
      <div
        style={{
          display: "flex",
        }}
      >
        {/* Sidebar with Blog Entries */}
        <aside
          style={{
            width: "250px",
            paddingRight: "20px",
            borderRight: "1px solid #A8DADC", // Light teal border
          }}
        >
          <h2 style={{ color: "#2C5D63", marginBottom: "10px" }}>Blog Posts</h2>
          <ul style={{ listStyle: "none", padding: 0 }}>
            {posts.map(({ node }) => (
              <li key={node.fields.slug} style={{ marginBottom: "10px" }}>
                <Link
                  to={node.fields.slug}
                  style={{
                    textDecoration: "none",
                    color: "#2C5D63", // Soft teal for links
                  }}
                >
                  {node.frontmatter.title}
                </Link>
                <p
                  style={{
                    color: "#777",
                    fontSize: "0.9rem",
                    margin: "5px 0 0 0",
                  }}
                >
                  {new Date(node.frontmatter.date).toLocaleDateString()}
                </p>
              </li>
            ))}
          </ul>
        </aside>

        {/* Main Content */}
        <main
          style={{
            flex: 1,
            paddingLeft: "20px",
          }}
        >
          {/* <h1 style={{ color: "#2C5D63" }}>Blog Posts</h1> */}
          {posts.map(({ node }) => (
            <article key={node.fields.slug} style={{ marginBottom: "40px" }}>
              <h2 style={{ color: "#2C5D63", marginBottom: "10px" }}>
                <Link
                  to={node.fields.slug}
                  style={{
                    textDecoration: "none",
                    color: "#2C5D63",
                  }}
                >
                  {node.frontmatter.title}
                </Link>
              </h2>
              <p style={{ color: "#777", fontSize: "0.9rem", margin: "5px 0" }}>
                {new Date(node.frontmatter.date).toLocaleDateString()}
              </p>
              <p style={{ color: "#4A4A4A" }}>
                {node.excerpt} {/* Display the excerpt of the post */}
              </p>
              <Link
                to={node.fields.slug}
                style={{
                  textDecoration: "none",
                  color: "#E63946", // A contrasting color for the "Read More" link
                  fontWeight: "bold",
                }}
              >
                Read More →
              </Link>
            </article>
          ))}
        </main>
      </div>
    </Layout>
  );
};

export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            date
          }
          excerpt(pruneLength: 200) # Add this to include an excerpt of the post
        }
      }
    }
  }
`;

export default IndexPage;